import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cls from 'classnames';
class PageLoader extends PureComponent {
    render() {
        const { spinning, className = '', style } = this.props;
        return (<div style={style} className={cls('seid-page-loader', 'spinning', className, { 'hidden': !spinning })}>
        <div className="loading-center">
          <div className="loading-center-absolute">
            <div className="point point-t"/>
            <div className="point point-r"/>
            <div className="point point-b"/>
            <div className="point point-l"/>
          </div>
        </div>
      </div>);
    }
}
PageLoader.defaultProps = {
    spinning: true,
};
PageLoader.propTypes = {
    spinning: PropTypes.bool,
    className: PropTypes.string,
};
export default PageLoader;
